import React from 'react';
import {
    Button,
    Select
} from 'antd';
import AttributeItem from './AttributeItem';
import { PartitionAlgorithm } from '@/constant/GraphMetrics'

const {
    Option
} = Select;

const topics = [{
    'value': 'default',
    "name": 'default'
}, {
    "value": 'around_single',
    "name": 'Exploring Local Neighbors',
}, {
    "value": 'key_players',
    "name": 'Detecting Key Players',
}, {
    "value": 'alliance_opposition',
    "name": 'Alliance and Opposition',
}, {
    "value": 'hidden_ties',
    "name": 'Revealing Hidden Ties',
}];

export default class StoryGeneration extends React.Component {
    state = {
        topic: topics[0].value,
        partition: 'default'
    }

    onTopicChange = (value) => {
        this.setState({topic: value});
    }

    onPartitionChange = (value) => {
        this.setState({partition: value});
    }

    regenerate = () => {
        // TODO: 
        const {topic, partition} = this.state;
        this.props.generateStory(topic, partition);
    }

    render() {
        const { topic, partition } = this.state;
        const { intl, initDone, schema } = this.props;
        const partitions = schema.filter(d => d.type === "categorical").map(d => d.field);
        partitions.unshift('default');
        partitions.push(...PartitionAlgorithm);
        return (
            <div>
                <AttributeItem name={initDone && intl.get("topic")}>
                    <Select
                        size="small"
                        style={{ width: '100%' }}
                        onChange={
                            this.onTopicChange
                        }
                        defaultValue={topic}
                        dropdownMatchSelectWidth={false}
                    >
                        {topics.map(({ value, name }) =>
                            <Option value={value} key={value}> {initDone && intl.get(name)} </Option>
                        )}
                    </Select>
                </AttributeItem>
                <AttributeItem name={initDone && intl.get("partition")}>
                    <Select
                        size="small"
                        style={{ width: '100%' }}
                        onChange={
                            this.onPartitionChange
                        }
                        defaultValue={partition}
                        dropdownMatchSelectWidth={false}
                    >
                        {partitions.map((partition) =>
                            <Option value={partition} key={partition}> {initDone && intl.get(partition) || partition} </Option>
                        )}
                    </Select>
                </AttributeItem>
                <div style={{width: '100%', display: 'inline-block'}}>
                    <Button size="small" style={{float:"right"}} onClick={this.regenerate}>{initDone && intl.get("regenerate")}</Button>
                </div>
            </div>
        );
    }
}